import * as THREE from 'three'
import { Stats } from 'three-stats'
import { CustomCSS3DObject, CustomCSS3DSprite, CustomCSS3DRenderer } from './CustomCSS3DRenderer'
import { DampingObjectController, DampingVector3Controller } from '../Three/Utils'
import { CardInfo, CardController } from './index'


class CarouselCardController extends CardController {

    constructor(
        angle: number, radius: number, z: number,
        momentum: number, attraction: number, relVelocity: number, staticThreshold: number = 1e-2) {
        super(angle, radius, z, momentum, attraction, relVelocity, staticThreshold)
    }
    getPosition () {
        return new THREE.Vector3(-this.radius * Math.cos(this.angle+Math.PI/2), this.radius * Math.sin(this.angle+Math.PI/2), -this.z)
    }
    getRotation () {
        return new THREE.Euler(Math.PI/2, -this.angle, Math.PI/2)
    }
    nonlinear (val: number) {
        return Math.sqrt(Math.abs(val)) * Math.sign(val) 
    }
    update () {
        // v
        this.angleV = this.attraction * this.nonlinear(this.angleTarget - this.angle) + this.momentum * this.angleV
        this.radiusV = this.attraction * this.nonlinear(this.radiusTarget - this.radius) + this.momentum * this.radiusV
        this.zV = this.attraction * this.nonlinear(this.zTarget - this.z) + this.momentum * this.zV
        // p
        this.angle += this.relVelocity * this.angleV * 0.1
        this.radius += this.relVelocity * this.radiusV
        this.z += this.relVelocity * this.zV
        // is the card static?
        let isPreviousAlmostStatic = this.isAlmostStatic
        this.isAlmostStatic =
            Math.abs(this.radiusTarget - this.radius)
            + Math.abs(this.angleTarget - this.angle)
            + Math.abs(this.zTarget - this.z) < this.staticThreshold
        // if previous status is active but present static, call the event function
        if (!isPreviousAlmostStatic && this.isAlmostStatic && this.afterStaticEvent) {
            this.afterStaticEvent()
            this.afterStaticEvent = undefined
        }
    }
}

class Card {
    mesh: THREE.Mesh
    info: CardInfo
    controller: CarouselCardController
    constructor(mesh: THREE.Mesh, info: CardInfo, controller: CarouselCardController) {
        this.mesh = mesh
        this.info = info
        this.controller = controller
        this.mesh.position.copy(this.controller.getPosition())
        this.mesh.rotation.copy(this.controller.getRotation())
    }
    updateByController() {
        this.controller.update()
        this.mesh.position.copy(this.controller.getPosition())
        this.mesh.rotation.copy(this.controller.getRotation())
    }
}

interface CarouselConfigs {

    cardSizeX: number,
    cardSizeY: number,

    cardRingRadius: number,
    cardRingFocusRadius: number,

    cardMomentum: number,
    cardAttraction: number,
    cardRelVelocity: number,

    wheelSpeedFactor: number,

    cameraDist: number

}

class CarouselManager {
    container: HTMLElement
    renderer: THREE.WebGLRenderer
    // controls: OrbitControls
    scene: THREE.Scene
    stats: Stats
    // controls: OrbitControls

    // cursor position
    mousePosX: number
    mousePosY: number
    pointer: THREE.Vector2

    // camera
    camera: THREE.PerspectiveCamera
    cameraBasicLookAt: THREE.Vector3
    cameraBasicPosition: THREE.Vector3
    // cameraLootAtController: DampingVector3Controller
    // cameraTargetPosition: THREE.Vector3
    // cameraWithMouseRotateFactor: number
    // cameraWithMouseMoveFactor: number

    // intersections
    raycaster: THREE.Raycaster
    intersectObjs: Array<THREE.Object3D>

    // css
    cssScene: THREE.Scene
    cssRenderer: CustomCSS3DRenderer | undefined

    // loading manager
    loadingManager: THREE.LoadingManager

    // card controllers
    cards: Array<Card>
    cardGroup: THREE.Group
    configs: CarouselConfigs
    // // 0 - hide
    // // 1 - carousel
    // // 2 - focused carousel
    // cardGroupStatus: number

    pickedCardIndex: number
    carouselRotateAngle: number
    lastWheelTime: number
    onCardFocus: ((index: number) => any) | undefined
    onCardRouteJump: ((url: string) => any) | undefined

    // camera animations
    cameraPositionController: DampingVector3Controller
    cameraWithMouseRotateFactor: number

    // scene pausing
    isScenePaused: boolean

    constructor(_container: HTMLElement,
        cardInfos: Array<CardInfo>,
        onLoad?: () => void,
        onProgress?: (url: string, loaded: number, total: number) => void,
        onError?: (url: string) => void,
        onCardFocus?: (index: number) => any,
        onCardRouteJump?: (url: string) => any,
        configs: CarouselConfigs = {
            cardSizeX: 70,
            cardSizeY: 50,
            cardRingRadius: 90,
            cardRingFocusRadius: 70,
            cardMomentum: 0.87,
            cardAttraction: 0.25,
            cardRelVelocity: 0.18,
            wheelSpeedFactor: 0.4,
            cameraDist: 220,
        },
        ) {
        const self = this

        // *********** basic settings ***********
        this.container = _container
        this.camera = new THREE.PerspectiveCamera( 45, window.innerWidth / window.innerHeight, 1, 5000 )
        this.renderer = new THREE.WebGLRenderer( {alpha: true} )
        this.renderer.setClearColor( 0xffffff )
        this.renderer.setPixelRatio( window.devicePixelRatio )
        this.renderer.setSize(window.innerWidth, window.innerHeight)
        this.renderer.domElement.style.position = 'absolute'
        this.renderer.domElement.style.top = '0px'
        this.renderer.domElement.style.left = '0px'
        this.container.appendChild( this.renderer.domElement )
        this.renderer.shadowMap.enabled = true
        this.renderer.shadowMap.type = THREE.PCFSoftShadowMap
        // TODO: (debug stats)
        this.stats = new Stats()
        this.stats.domElement.style.zIndex = '100'
        this.container.appendChild( this.stats.domElement )
        // this.controls = new OrbitControls(this.camera, this.container)
        this.cameraBasicLookAt = new THREE.Vector3(0)
        this.cameraBasicPosition = new THREE.Vector3(0, 0, configs.cameraDist)
        this.camera.position.copy(this.cameraBasicPosition)
        this.camera.lookAt(this.cameraBasicLookAt)

        this.scene = new THREE.Scene()
        this.cssScene = new THREE.Scene()

        // loading manager
        this.loadingManager = new THREE.LoadingManager(() => {
            if (onLoad) {
                onLoad()
                this.makeCarousel()
            }
        }, (url: string, loaded: number, total: number) => {
            if (onProgress) {
                onProgress(url, loaded, total)
            }
        }, (url) => {
            if (onError) {
                onError(url)
            }
        })

        
        // *********** cards ***********
        this.intersectObjs = []
        this.cardGroup = new THREE.Group()
        this.cards = []
        this.configs = configs
        const cardGeom = new THREE.PlaneGeometry(configs.cardSizeX, configs.cardSizeY)
        cardInfos.forEach((cardInfo, cardIndex) => {
            let tx = new THREE.TextureLoader(this.loadingManager).load(cardInfo.imgUrl)
            const card =  new Card(
                    new THREE.Mesh(cardGeom, new THREE.MeshBasicMaterial(
                        {
                            map: tx,
                            side: THREE.DoubleSide
                        }
                    )),
                    cardInfo,
                    new CarouselCardController(
                        Math.random()*2 + cardIndex * Math.PI * 2 / cardInfos.length, 200, Math.random() * 200 + 100,
                        this.configs.cardMomentum,
                        this.configs.cardAttraction,
                        this.configs.cardRelVelocity
                    )
                )
            card.mesh.castShadow = true
            card.mesh.receiveShadow = true
            this.cards.push(card)
            this.cardGroup.add(card.mesh)
            this.intersectObjs.push(card.mesh)
        })
        this.cardGroup.rotation.x = Math.PI / 2
        this.scene.add(this.cardGroup)

        this.carouselRotateAngle = 0
        this.pickedCardIndex = 0
        this.lastWheelTime = 0
        // this.cardGroupStatus = 0
        this.onCardFocus = onCardFocus
        this.onCardRouteJump = onCardRouteJump


        // *********** raycast intersections ***********
        this.raycaster = new THREE.Raycaster()
        

        // *********** camera animations ***********
        this.cameraPositionController = new DampingVector3Controller(new THREE.Vector3(), 0.9, 0.002)
        this.cameraWithMouseRotateFactor = 35

        // *********** events ***********
        this.mousePosX = 0
        this.mousePosY = 0
        this.pointer = new THREE.Vector2()

        // *********** animate ***********
        this.isScenePaused = false
        this.resumeScene()
    }

    animate = () => {
        if (this.isScenePaused) {
            return
        }
        requestAnimationFrame( this.animate )
        // damping controllers
        // this.updatecornerObject()
        // this.updateCamera()
        this.cards.forEach(card => {
            card.updateByController()
        })
        this.render()
        this.stats.update()
        // this.controls.update()
    }

    render ()
    {
        this.renderer.render(this.scene, this.camera)
        if (this.cssRenderer) {
            this.cssRenderer.render(this.cssScene, this.camera)
        }
    }

    onWindowResize = () => {
        this.camera.aspect = window.innerWidth / window.innerHeight
        this.camera.updateProjectionMatrix()
        this.renderer.setSize( window.innerWidth, window.innerHeight )
        if (this.cssRenderer) {
            this.cssRenderer.setSize( window.innerWidth, window.innerHeight )
        }
    }

    onMouseWheel = (e: WheelEvent) => {
        this.lastWheelTime = (new Date()).getTime()
        if (e.deltaY > 0) {
            this.carouselRotateAngle += this.configs.wheelSpeedFactor
        } else {
            this.carouselRotateAngle -= this.configs.wheelSpeedFactor
        }
        const self = this
        setTimeout(() => {
            var newTime = (new Date()).getTime()
            if (newTime - self.lastWheelTime >= 199) {
                // to int
                var newCarouselRotateAngle = Math.round(self.carouselRotateAngle)
                var newIndex = ((newCarouselRotateAngle % self.cards.length) + self.cards.length) % self.cards.length
                console.log('AAAAAAAAAA', newIndex, newCarouselRotateAngle)
                self.pickCard(self.pickedCardIndex, newIndex)
            }
        }, 200)
        this.makeCarousel()
    }
    
    onMouseMove = (e: MouseEvent) => {
        this.mousePosX = e.clientX
        this.mousePosY = e.clientY
        this.pointer.x = ( e.clientX / window.innerWidth ) * 2 - 1;
		this.pointer.y = - ( e.clientY / window.innerHeight ) * 2 + 1;

        // this.raycaster.setFromCamera( this.pointer, this.camera )
        // const intersects = this.raycaster.intersectObjects( this.intersectObjs, false )
        // if (intersects.length == 1) {
        //     console.log('intersected:', intersects[0])
        // }
    }

    onClick = (e: MouseEvent) => {
        this.raycaster.setFromCamera( this.pointer, this.camera )
        const intersects = this.raycaster.intersectObjects( this.intersectObjs, false )
        if (intersects.length >= 1) {
            // get index of clicked card
            var clickedCardIndex = 0
            for (; clickedCardIndex < this.cards.length; clickedCardIndex++) {
                if (intersects[0].object == this.cards[clickedCardIndex].mesh) {
                    break
                }
            }
            this.pickCard(this.pickedCardIndex, clickedCardIndex)
        }
    }

    makeCarousel () {
        var angleUnit = Math.PI * 2 / this.cards.length
        this.cards.forEach((card, index) => {
            card.controller.angleTarget = (index - this.carouselRotateAngle) * angleUnit
            card.controller.radiusTarget = this.configs.cardRingRadius
            card.controller.zTarget = 0
        })
    }

    makeCarouselFocus (oldIndex: number, newIndex: number) {
        if (newIndex < oldIndex) {
            newIndex += this.cards.length
        }
        this.carouselRotateAngle = Math.round(this.carouselRotateAngle)
        if (newIndex <= oldIndex + this.cards.length/2) {
            // clockwise
            this.carouselRotateAngle += (newIndex - oldIndex)
        } else {
            // anti-clockwise
            this.carouselRotateAngle += (newIndex - oldIndex - this.cards.length)
        }
        this.makeCarousel()
    }

    pickCard (oldIndex: number, newIndex: number) {
        /**
         * focus on a single card by pulling it out a little bit
         */
        if (newIndex < 0 || newIndex > this.cards.length - 1) {
            return
        }
        this.makeCarouselFocus(oldIndex, newIndex)
        if (this.onCardFocus) {
            this.onCardFocus(newIndex)
        }
        this.pickedCardIndex = newIndex
    }

    updateCamera() {
        const dampPos = (new THREE.Vector3(
            this.pointer.x * this.cameraWithMouseRotateFactor,
            this.pointer.y * this.cameraWithMouseRotateFactor,
            -this.pointer.y * this.cameraWithMouseRotateFactor,
        ))

        this.cameraPositionController.update(dampPos)
        dampPos.copy(this.cameraPositionController.currentVec3).add(this.cameraBasicPosition)
        // this.cameraLootAtController.update(this.cameraTargetPosition)
        this.camera.position.copy(dampPos)
        this.camera.lookAt(this.cameraBasicLookAt)
        // var vel = this.cameraLootAtController.velocity
        // this.camera.position.x = vel.x * this.cameraWithMouseMoveFactor
        // this.camera.position.y = 200 - vel.y * this.cameraWithMouseMoveFactor
    }

    pauseScene () {
        this.isScenePaused = true
        const self = this
        window.removeEventListener( 'resize', self.onWindowResize )
        window.removeEventListener( 'wheel', self.onMouseWheel )
        window.removeEventListener('mousemove', self.onMouseMove )
        window.removeEventListener( 'click', self.onClick )
    }
    resumeScene () {
        this.isScenePaused = false
        this.animate()
        const self = this
        window.addEventListener( 'resize', self.onWindowResize )
        window.addEventListener( 'wheel', self.onMouseWheel )
        window.addEventListener('mousemove', self.onMouseMove )
        window.addEventListener( 'click', self.onClick )
    }

}


export {
    CarouselManager
}