

























import * as THREE from 'three'
import Vue from 'vue'
import { CardInfo } from '../scripts/FrontPage3.0'
import { CarouselManager } from '../scripts/FrontPage3.0/Carousel'

type Data = {
    cm: CarouselManager | null,
    loadingStat: {
        loaded: number,
        total: number,
        isLoaded: boolean,
        noLoadingStat: boolean
    },
    cardInfos: Array<CardInfo>
}

export default Vue.extend({
    data(): Data {
        return {
            cm: null,
            loadingStat: {
                loaded: 0,
                total: 1,
                isLoaded: false,
                noLoadingStat: false
            },
            cardInfos: new Array<CardInfo>()
        }
    },
    mounted() {
        const self = this
        for (let i = 0; i < 10; i++) {
            this.cardInfos.push(
                {
                    imgUrl: `card${Math.floor(i%3)+1}.jpg`,
                    url: 'fakeUrl_' + i
                }
            )
        }
        if (this.$refs['canvas-container'] instanceof HTMLElement) {
            this.cm = new CarouselManager(
                this.$refs['canvas-container'],
                this.cardInfos,
                () => {
                    console.log('loaded')
                    self.loadingStat.isLoaded = true
                    setTimeout(() => {
                        self.loadingStat.noLoadingStat = true
                    }, 501)
                },
                (_, loaded, total) => {
                    this.loadingStat.loaded = loaded
                    this.loadingStat.total = total
                },
                (url) => {
                    console.error('failed to load asset: ' + url)
                    self.loadingStat.isLoaded = true
                },
                (index) => {
                    console.log('focused on card: ', index)
                }
            )
            // if (this.$refs['dom3d-container'] instanceof HTMLElement && this.$refs['dom3d-test'] instanceof HTMLElement) {
            //     this.fpm.registerCSSCoordinator(this.$refs['dom3d-container'], this.$refs['dom3d-test'], new THREE.Vector3(-200, 200, 0))
            // }
        }
    },
    methods: {
        onGlassClick() {
            alert('glass clicked!')
        }
    }
})
